<template>
  <section class="login">
    <header>欢迎登录</header>
    <ul class="input-box">
      <li class="item username">
        <span class="iconfont">&#xe631;</span>
        <input v-model="account" type="text" placeholder="用户名/邮箱">
        <span
          @click="clearAccountValue"
          class="iconfont cancel"
        >
        &#xe621;
        </span>
      </li>
      <li class="item password">
        <span class="iconfont">&#xe654;</span>
        <input v-model="password" type="password" placeholder="密码">
        <span @click="clearPasswordValue" class="iconfont cancel">&#xe621;</span>
      </li>
    </ul>
    <div @click="getLogin" class="submit">登录</div>
  </section>
</template>

<script>
import { RegisterValidator } from '../../model/validator'
import { Message } from 'element-ui'
import axios from 'axios'
import { configs } from '../../api/config'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'Login',
  data () {
    return {
      account: '',
      password: '',
      cancelBlock: false
    }
  },
  computed: {
    ...mapState([
      'isLogin',
      'username',
      'token'
    ])
  },
  methods: {
    ...mapMutations([
      'changeToken',
      'changeUsername',
      'changeLoginState'
    ]),
    clearAccountValue () {
      this.account = ''
    },
    clearPasswordValue () {
      this.password = ''
    },
    getLogin () {
      const type = RegisterValidator.getType(this.account)
      const CancelToken = axios.CancelToken
      const source = CancelToken.source()
      console.log('type is', type)
      console.log('configs.baseUrl is', configs.baseUrl)
      axios({
        url: configs.baseUrl + 'v1/token',
        method: 'POST',
        data: {
          account: this.account,
          password: this.password,
          type: type
        }
      }, {
        cancelToken: source.token
      })
        .then(res => {
          Message({
            message: '登录成功',
            type: 'success'
          })
          let token = res.data.token
          let username = res.data.username
          /**
           * 登录成功，用户名和token写入缓存和vuex中
           */
          localStorage.setItem('username', username)
          localStorage.setItem('token', token)
          this.changeToken(token)
          this.changeUsername(username)
          this.changeLoginState(true)
          this.$router.push({name: 'Home'})
        })
        .catch(err => {
          console.log(err.response)
        })
      source.cancel()
    }
  }
}
</script>

<style lang="scss" scoped>
  .login{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 240px;
    height: 240px;
    padding: 20px 30px;
    border: 1px solid #dfdfdf;
    border-radius: 5px;
    background-color: #fff;
    header{
      position: relative;
      width: 240px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      color: #f40;
      &:after{
        content: "";
        display: block;
        position: absolute;
        top: 40px;
        left: 0px;
        width: 240px;
        height: 1px;
        background-color: #FFE4B5;
      }
    }
    .input-box{
      width: 240px;
      height: 120px;
      margin-top: 30px;
      .item{
        box-sizing: border-box;
        width: 240px;
        height: 30px;
        border: 1px solid #FFE4B5;
        color: #666;
        display: flex;
        flex-direction: row;
        align-items: center;
        span{
          color: #999!important;
          margin-left: 4px;
        }
        input{
          margin-left: 10px;
          border: 0;
          width: 180px;
          color: #666;
        }
        &:nth-of-type(n+2){
          margin-top: 10px;
        }
        .cancel{
          cursor: pointer;
        }
      }
    }
    .submit{
      width: 240px;
      height: 30px;
      line-height: 30px;
      margin-top: 20px;
      text-align: center;
      background-color: #f40;
      color: #fff;
      border-radius: 5px;
      cursor: pointer;
    }
  }
</style>



